
























































import Vue from 'vue'
import { mapGetters, mapMutations, mapState } from 'vuex'
import { formatDate } from '@/utils/date'
import { Button } from '@/components/Button'

export default Vue.extend({
  name: 'Edition',
  components: {
    Button
  },
  props: {
    taskId: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      newComment: {
        author: null,
        message: null
      }
    }
  },
  computed: {
    ...mapState({
      task(state) {
        return this.getTaskById(this.taskId)
      }
    }),
    ...mapGetters(['getTaskById'])
  },
  methods: {
    formatDate,
    handleSave(e: Event) {
      e.preventDefault()
      if (this.newComment.author && this.newComment.message) {
        this.addNewComment({ id: this.taskId, comment: this.newComment })
        this.$forceUpdate()
      } else {
        alert('Please fill author and comment fields.')
      }
    },
    ...mapMutations(['addNewComment'])
  }
})
